.app-wrapper {
    display: grid;

    width: 1200px;
    height: 800px;
    grid-template-rows: [header] 60px [content] 720px;
    grid-template-columns: [leftgutter] 1fr [navbar] 2fr [profile] 9fr [rightgutter] 1fr;
    grid-gap: 15px;
    margin-top: 15px;
    margin: 0 auto;

}

.app-wrapper-content {
    grid-column: profile;
    grid-row: content;
}
.Preloader {
padding: 400px;
}





