.content {
    grid-gap: 15px;
    grid-template-columns: repeat(14, 1fr);
    grid-template-rows: repeat(14, 1fr);

}

.uppic1{
    grid-column: 1/15;
    grid-row: 1;
}
.uppic {
    height: 150px;
    width: 100%;

}
.info {
    height: 175px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(4, 1fr);
    margin-top: 20px;

}

.avatar1 {
    display: grid;
    grid-column: 1/3;
    grid-row: 1/3;
}
.avatar {

    width: 40%;

}
.article {

    margin-right: 10px;
    grid-column: 2/6;
    grid-row: 1/3;

}

.Name {
    margin: 0px;
    margin-bottom: 20px;
    font-family: David;
    color: black;
    font-size: xx-large;
    font-weight: bold;
}

.about, .contacts {

    margin:0px;
    font-family: David;
    color:grey;
    font-size: x-large;
    font-weight: lighter;

}



