.nav {
    grid-column: navbar;
    grid-row: 2/9;
    background-color: springgreen;

}

.item {
    text-align: left;
    padding-left: 15px;
    font-family:  "Times New Roman";
    color: black;
    padding-top: 10px;
    font-size: x-large;
    font-weight: bold;
    font-style: italic;



}
